* {
	font-family: 'Lora', serif;
	font-size: 25px;
	a {
		text-decoration: none;
		color: black;
		&:visited {
			color: black; } }
	p {
		margin: 0 0 10px 0;
		span {
			color: orange; } }
	.nextPage,.prevPage {
		display: flex;
		flex-direction: row;
		a {
			display: flex;
			justify-content: center;
			align-items: center;
			color: #009AD0;
			img {
				height: 15px;
				width: auto; } } }
	h1 {
		font-size: 65px;
		color: red;
		margin-top: 25px;
		span {
			font-size: 65px;
			color: orange; } }
	h2 {
		font-size: 40px;
		span {
			font-size: 40px;
			color: orange; } }
	h3 {
		color: orange; }
	h4 {
		font-size: 30px;
		color: #BD8913; }
	h5 {
		color: blue;
		font-size: 30px; }
	.card {
		img {
			margin: 10px auto 0;
			width: 200px;
			height: 200px; } }
	.cardMain {
		img {
			width: 300px;
			height: 350px; } }
	.row {
		margin: 50px 0; }
	.modal {
		.modal-dialog {
 }			// width: 100%
		.modal-body {
			img {
 } } } }				// width: 100%
header {
	margin-top: 50px;
	img {
		max-width: 100%; }
	.language-selector {
		p {
			color: #009AD0; } } }
main {
	.mainImg {
		display: flex;
		justify-content: center;
		width: 100%;
		position: relative; }
	.mainImgMobile {
		display: none; }
	.bigMainImg {
		display: flex;
		justify-content: center; }
	.nextFlex {
		display: flex;
		flex-direction: column;
		h1 {
			margin: 0 auto; }
		p {
			margin-bottom: 10px; }
		blockquote {
			margin-bottom: 10px; } }
	.price {
		img {
			max-height: 200px; } }
	.signUp {
		display: flex;
		justify-content: center;
		img {
			max-width: 100%; }
		h1 {
			white-space: nowrap; } }
	.contacts {
		display: flex;
		flex-direction: column;
		align-items: center;
		.imgContainer {
			display: flex;
			justify-content: center;
			img {
				height: 50px;
				width: auto; } } } }
footer {
	padding: 10px 0;
	background: #f3f3bc;
	a:hover {
		text-decoration: none; }
	.row {
		margin: 0; }
	.aboutMe {
		img {
			opacity: 0.5; } }
	.goaExcursions {
		img {
			width: 250px; } } }

@import  'media';
